import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Layout } from 'antd';
import Sidebar from './../../../components/sidebar/sidebar';
import Topbar from './../../../components/header/header';
import Calendars from './../../Student/calendar/calendar';
import Loading from './../loading/loading';
import { withRouter, Route, Switch } from 'react-router-dom';
import Test from './../test/test';
import './../../../styles/components/home.less';
import { logout } from './../../../redux/actions/authentication';
import {
	connectWebsocket,
	absencesListener,
	sendMarkAbsence,
	sendGetAbsences,
	sendJustifyAbsence,
	sendGetTests,
	testsListener,
	sendGetClasses,
	classesListener,
	sendGetDisciplines,
	sendGetTeacherDisciplines,
	disciplineListener,
	sendCreateLesson,
	lessonListener,
	studentsListener,
	sendGetAbsenceTypes,
	themeListener,
	moduleListener,
	sendGetModules // TODO REMOVE
} from './../../../redux/actions/websocket';
import { shallowEqual } from 'shouldcomponentupdate-children';
import Loadable from 'react-loadable';

const { Content } = Layout;


const Settings = Loadable({
	loader: () => import('./../settings/settings'),
	loading: Loading,
	// delay: 300
});

const AlunoStats = Loadable({
	loader: () => import('./../../Student/stats/aluno-stats'),
	loading: Loading,
	// delay: 300
});

const ClassManagement = Loadable({
	loader: () => import('./../../Teacher/ClassManagement/class-management'),
	loading: Loading,
	// delay: 300
});

const MarkTest = Loadable({
	loader: () => import('./../../Teacher/MarkTest/mark-test'),
	loading: Loading,
	// delay: 300
});

const LessonMagement = Loadable({
	loader: () => import('./../../Teacher/LessonManagement/lesson-management'),
	loading: Loading,
	// delay: 300
});

class Home extends Component {
	constructor(props) {
		super(props);

		this.state = {
			collapsed: true,
			dark: true,
			isMobile: false,
			initialFetchDone: false
		};
	}

	componentDidMount() {
		this.props.connectWebsocket(this.props.token);
		this.props.absencesListener();
		this.props.testsListener();
		this.props.classesListener();
		this.props.disciplineListener();
		this.props.lessonListener();
		this.props.studentsListener();
		this.props.sendGetAbsenceTypes();
		this.props.themeListener();
		this.props.moduleListener();
	}

	shouldComponentUpdate(nextProps, nextState) {
		return shallowEqual(this.props, nextProps, this.state, nextState);
	}

	componentWillReceiveProps(nextProps) {
		if (nextProps.storageReady) {
			if (!this.state.initialFetchDone && this.props.role === 'ALUNO') {
				this.props.sendGetAbsences();
				this.props.sendGetTests();

				this.setState({ initialFetchDone: true });
			} else if(!this.state.initialFetchDone && this.props.role === 'PROFESSOR') {
				this.testConnection();

				this.setState({ initialFetchDone: true });
			}
		}
	}

	testConnection = () => {
		this.props.sendGetAbsences();
		this.props.sendGetTests();
		this.props.sendGetClasses();
		this.props.sendGetDisciplines();
		this.props.sendGetTeacherDisciplines();
		this.props.sendGetModules();

		console.log('TEST ENDED');
	}

	toggle = () => {
		this.setState({
			collapsed: !this.state.collapsed
		});
	};

	render() {
		var theme;

		if (this.state.collapsed) {
			if (this.state.dark) {
				theme = 'logo-title dark hidden';
			} else {
				theme = 'logo-title light hidden';
			}
		} else {
			if (this.state.dark) {
				theme = 'logo-title dark';
			} else {
				theme = 'logo-title light';
			}
		}

		return (
			<Layout style={{ height: '100vh', overflow: 'hidden' }} className="full-size">
				<Sidebar
					history={this.props.history}
					trigger={null} // Remove sidebar trigger
					onCollapse={this.toggle}
					collapsible
					collapsedWidth={this.state.isMobile ? '0px' : '80'}
					collapsed={this.state.collapsed}
					className="sider-shadow"
					breakpoint="md"
					theme={this.state.dark ? 'dark' : 'light'}
					width="256"
					role={this.props.role}
				/>
				<Layout style={{ height: '100vh', overflow: 'hidden' }}>
					<Topbar toggle={this.toggle} collapsed={this.state.collapsed} />
					<Content className="content">
						<Switch>
							<Route path={`${this.props.match.path}settings`} component={Settings} />
							<Route path={`${this.props.match.path}upload`} render={() => <Test />} />
							{this.props.role === 'ALUNO' ? (
								<div style={{ height: '100%'}}>
									<Route path={`${this.props.match.path}dashboard`} render={() => <AlunoStats />} />
									<Route
										path={`${this.props.match.path}calendar`}
										render={() => (
											<Calendars
												role={this.props.role}
												tests={this.props.tests}
												absences={this.props.absences}
											/>
										)}
									/>
								</div>
							) : null}

							{this.props.role === 'PROFESSOR' ? (
								<div style={{ height: '100%'}}>
									<Route
										path={`${this.props.match.path}dashboard`}
										render={() => <ClassManagement />}
									/>
									<Route path={`${this.props.match.path}calendar`} render={() => <MarkTest />} />
									<Route path={`${this.props.match.path}lessons`} render={() => <LessonMagement />} />
								</div>
							) : null}
						</Switch>
					</Content>
				</Layout>
			</Layout>
		);
	}
}

const mapDispatchToProps = {
	connectWebsocket,
	absencesListener,
	sendMarkAbsence,
	sendJustifyAbsence, // TODO REMOVE UNUSED
	sendGetAbsences,
	sendGetTests,
	testsListener,
	sendGetClasses,
	classesListener,
	sendGetDisciplines,
	sendGetTeacherDisciplines,
	disciplineListener,
	sendCreateLesson,
	lessonListener,
	studentsListener,
	sendGetAbsenceTypes,
	logout,
	themeListener,
	moduleListener,
	sendGetModules // TODO REMOVE
};

const mapStateToProps = (state) => {
	return {
		token: state.authentication.token,
		role: state.authentication.role,
		storageReady: state.connect.storageReady,
		absences: state.absences.absences,
		tests: state.tests.tests
	};
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Home));



