import React, { Component } from 'react';
import { Tabs } from 'antd';
import AbsenceCalendar from './../../../components/calendar-components/absence-calendar';
import TestsCalendar from './../../../components/calendar-components/tests-calendar';
import MealsCalendar from './../../../components/calendar-components/meals-calendar';
import PerfectScrollbar from 'react-perfect-scrollbar';
import 'react-perfect-scrollbar/dist/css/styles.css';
import { shallowEqual } from 'shouldcomponentupdate-children';

const { TabPane } = Tabs;

class Calendars extends Component {
	parentWidth = (e) => {
		this.props.content;
	};

	shouldComponentUpdate(nextProps, nextState) {
		return shallowEqual(this.props, nextProps, this.state, nextState);
	}

	render() {
		var numbers = this.props.parentHeight - 135;

		return (
			<PerfectScrollbar>
				<div className="cal" style={{ height: this.props.parentHeight }}>
					<Tabs style={{ height: this.props.parentHeight }} defaultActiveKey="1" size="large">
						{this.props.role === 'ALUNO' ? (
							<TabPane style={{ height: numbers + 'px' }} tab="Faltas" key="1">
								<PerfectScrollbar>
									<AbsenceCalendar absences={this.props.absences} />
								</PerfectScrollbar>
							</TabPane>
						) : null}
						<TabPane style={{ height: numbers + 'px' }} tab="Testes" key="2">
							<PerfectScrollbar>
								<TestsCalendar tests={this.props.tests} role={this.props.role} />
							</PerfectScrollbar>
						</TabPane>
						<TabPane style={{ height: numbers + 'px' }} tab="Refeições" key="3">
							<PerfectScrollbar>
								<MealsCalendar />
							</PerfectScrollbar>
						</TabPane>
					</Tabs>
				</div>
			</PerfectScrollbar>
		);
	}
}

export default Calendars;



