import React, { Component } from 'react';
import UserLabel from './header-components/user-label';
import Notification from './header-components/notification';
// import Search from './header-components/search';
import { Layout, Icon } from 'antd';
import { shallowEqual } from 'shouldcomponentupdate-children';

const { Header } = Layout;

class Topbar extends Component {

	shouldComponentUpdate(nextProps, nextState) {
		return shallowEqual(this.props, nextProps, this.state, nextState);
	}

	render() {
		return (
			<Header className="header">
				<Icon
					style={{ paddingTop: '4px' }}
					className="trigger"
					type={this.props.collapsed ? 'menu-unfold' : 'menu-fold'}
					onClick={this.props.toggle}
				/>
				<div className="user-options-container">
					{/* <Search /> */}
					<Notification />
					<UserLabel
						image="https://cdn.discordapp.com/avatars/313010829790412800/57e7c5d16250f158d483b4c1869844b7.png?size=128"
						name="Tiago Lima"
					/>
				</div>
			</Header>
		);
	}
}

export default Topbar;



